import React, { useState, useEffect } from 'react';
import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';
import Login from './components/Login';
import AdministracionProductos from './components/AdministracionProductos';
import ConsultaCotizaciones from './components/ConsultaCotizaciones';
import Logs from './components/Logs';
import NavBar from './components/NavBar';

function App() {
  const [mostrarMenu, setMostrarMenu] = useState(false);
  const [usuario, setUsuario] = useState(null);
  const [rutaActual, setRutaActual] = useState(window.location.pathname);

  const handleInicioSesion = (usuario) => {
    setMostrarMenu(true);
    setUsuario(usuario);
  };

  const handleCerrarSesion = () => {
    setMostrarMenu(false);
    setUsuario(null);
  };

  useEffect(() => {
    setRutaActual(window.location.pathname);
  }, [window.location.pathname]);

  return (
    <BrowserRouter>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <div className="d-flex justify-content-end">
              {rutaActual === '/login' ? (
                <button className="btn btn-secondary" onClick={() => window.location.href = '/consulta-cotizaciones'}>
                  Regresar
                </button>
              ) : !mostrarMenu ? (
                <button className="btn btn-primary" onClick={() => window.location.href = '/login'}>
                  Iniciar Sesión
                </button>
              ) : null}
            </div>
          </div>
        </div>
        {mostrarMenu && (
          <NavBar mostrarMenu={mostrarMenu} handleCerrarSesion={handleCerrarSesion} />
        )}
        <Routes>
          <Route path="/" element={<Navigate to="/consulta-cotizaciones" replace />} />
          <Route path="/login" element={<Login handleInicioSesion={handleInicioSesion} />} />
          <Route
            path="/consulta-cotizaciones"
            element={<ConsultaCotizaciones />}
          />
          <Route
            path="/logs"
            element={
              mostrarMenu ? <Logs /> : <Navigate to="/login" replace />
            }
          />
          <Route
            path="/AdministracionProductos"
            element={
              mostrarMenu ? <AdministracionProductos /> : <Navigate to="/login" replace />
            }
          />
        </Routes>
      </div>
    </BrowserRouter>
  );
}

export default App;