import React, { useState, useEffect } from 'react';
import { db } from '../firebase';
import { collection, onSnapshot } from 'firebase/firestore';

const Logs = () => {
  const [logs, setLogs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const logsRef = collection(db, 'logs');
    const unsubscribe = onSnapshot(
      logsRef,
      (snapshot) => {
        const logsData = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
        setLogs(logsData);
        setLoading(false);
      },
      (error) => {
        setError(error.message);
        setLoading(false);
      }
    );
    return unsubscribe;
  }, []);

  if (loading) {
    return <div>Cargando...</div>;
  }

  if (error) {
    return <div>Error: {error}</div>;
  }

  return (
    <div>
      <h2>Logs</h2>
      <table className="table table-striped table-hover">
        <thead className="thead-dark">
          <tr>
            <th>Tipo</th>
            <th>Correo del usuario</th>
            <th>Producto</th>
            <th>Fecha</th>
          </tr>
        </thead>
        <tbody>
          {logs.map((log) => (
            <tr key={log.id}>
              <td>{log.tipo}</td>
              <td>{log.correo}</td>
              <td>
                {log.producto && (
                  <ul>
                    {log.producto.proveedor && <li>Proveedor: {log.producto.proveedor}</li>}
                    {log.producto.sku && <li>SKU: {log.producto.sku}</li>}
                    {log.producto.descripcion && <li>Descripción: {log.producto.descripcion}</li>}
                    {log.producto.tipo && <li>Tipo: {log.producto.tipo}</li>}
                    {log.producto.subtipo && <li>Subtipo: {log.producto.subtipo}</li>}
                    {log.producto.costo && <li>Costo: {log.producto.costo}</li>}
                  </ul>
                )}
              </td>
              <td>{log.fecha}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Logs;