import React, { useState, useEffect } from 'react';
import { db } from '../firebase';
import { collection, onSnapshot, addDoc, doc, setDoc, deleteDoc, getDocs } from 'firebase/firestore';
import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import { getAuth } from 'firebase/auth';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const auth = getAuth();

const AdministracionProductos = () => {
  const [productos, setProductos] = useState([]);
  const [proveedor, setProveedor] = useState('');
  const [tipo, setTipo] = useState('');
  const [subtipo, setSubtipo] = useState('');
  const [sku, setSku] = useState('');
  const [descripcion, setDescripcion] = useState('');
  const [costo, setCosto] = useState(0);
  const [idEditar, setIdEditar] = useState(null);
  const [tipoProducto, setTipoProducto] = useState('');
  const [pagina, setPagina] = useState(1);
  const [productosPorPagina, setProductosPorPagina] = useState(10);
  const [orden, setOrden] = useState('asc');

  useEffect(() => {
    const productosRef = collection(db, 'productos');
    const unsubscribe = onSnapshot(
      productosRef,
      (snapshot) => {
        if (!snapshot.empty) {
          const productos = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
          setProductos(productos);
        } else {
          setProductos([]); // Si la colección está vacía
        }
      },
      (error) => {
        console.error('Error al obtener productos:', error);
        setProductos([]); // Manejar el error limpiamente
      }
    );

    return () => unsubscribe(); // Limpiar el listener al desmontar el componente
  }, []);

  const agregarLog = async (tipo, correo, producto) => {
    try {
      const logsRef = collection(db, 'logs');
      await addDoc(logsRef, {
        tipo,
        correo,
        producto,
        fecha: new Date().toLocaleString(),
      });
    } catch (error) {
      console.error('Error al agregar log:', error);
    }
  };

  const agregarProducto = async () => {
    if (proveedor && tipo && (tipo.toLowerCase() !== 'cama' && tipo.toLowerCase() !== 'sala' && tipo.toLowerCase() !== 'comedor' || subtipo) && sku && descripcion && costo) {
      try {
        const productosRef = collection(db, 'productos');
        const querySnapshot = await getDocs(productosRef);
        const productos = querySnapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));

        const productoExistente = productos.find((producto) => producto.sku === sku || producto.descripcion === descripcion);

        if (productoExistente) {
          alert('El SKU o la descripción del producto ya existen. Por favor, ingrese un valor diferente.');
        } else {
          await addDoc(productosRef, {
            proveedor,
            tipo,
            subtipo: tipo.toLowerCase() === 'cama' || tipo.toLowerCase() === 'sala' || tipo.toLowerCase() === 'comedor' ? subtipo : '',
            sku,
            descripcion,
            costo,
          });
          const usuarioLogeado = auth.currentUser;
          const correo = usuarioLogeado.email;
          const productoAgregado = {
            proveedor,
            tipo,
            subtipo: tipo.toLowerCase() === 'cama' || tipo.toLowerCase() === 'sala' || tipo.toLowerCase() === 'comedor' ? subtipo : '',
            sku,
            descripcion,
            costo,
          };
          await agregarLog('agregar', correo, productoAgregado);
          setProveedor('');
          setTipo('');
          setSubtipo('');
          setSku('');
          setDescripcion('');
          setCosto(0);
          setIdEditar(null); // Limpiar el ID de edición
          toast.success('Producto agregado con éxito');
        }
      } catch (error) {
        console.error('Error al agregar producto:', error);
      }
    } else {
      alert('Por favor, llene todos los campos');
    }
  };

  const editarProducto = async (producto) => {
    try {
      const productosRef = doc(db, 'productos', producto.id);
      await setDoc(productosRef, {
        proveedor: producto.proveedor,
        tipo: producto.tipo,
        subtipo: producto.subtipo,
        sku: producto.sku,
        descripcion: producto.descripcion,
        costo: producto.costo,
      });
      const usuarioLogeado = auth.currentUser;
      const correo = usuarioLogeado.email;
      await agregarLog('editar', correo, producto);
      setProveedor('');
      setTipo('');
      setSubtipo('');
      setSku('');
      setDescripcion('');
      setCosto(0);
      setIdEditar(null); // Limpiar el ID de edición
      toast.success('Edición realizada con éxito');
    } catch (error) {
      console.error('Error al editar producto:', error);
    }
  };

  const eliminarProducto = async (producto) => {
    try {
      const productosRef = doc(db, 'productos', producto.id);
      await deleteDoc(productosRef);
      const usuarioLogeado = auth.currentUser;
      const correo = usuarioLogeado.email;
      await agregarLog('eliminar', correo, producto);
      toast.success('Producto eliminado con éxito');
    } catch (error) {
      console.error('Error al eliminar producto:', error);
    }
  };

  const handleEditar = (producto) => {
    setProveedor(producto.proveedor);
    setTipo(producto.tipo);
    setSubtipo(producto.subtipo);
    setSku(producto.sku);
    setDescripcion(producto.descripcion);
    setCosto(producto.costo);
    setIdEditar(producto.id);
  };

  const handleEliminar = (producto) => {
    if (window.confirm('¿Estás seguro de que deseas eliminar este registro?')) {
      eliminarProducto(producto);
    }
  };

  const ordenarProductos = () => {
    setOrden(orden === 'asc' ? 'desc' : 'asc');
  };

  const productosOrdenados = productos.sort((a, b) => {
    if (orden === 'asc') {
      return a.costo - b.costo;
    } else {
      return b.costo - a.costo;
    }
  });

  const tiposDeProducto = () => {
    const tipos = {};
    productos.forEach((producto) => {
      if (!tipos[producto.tipo]) {
        tipos[producto.tipo] = 1;
      } else {
        tipos[producto.tipo]++;
      }
    });
    return tipos;
  };

  const mostrarTodos = () => {
    setTipoProducto('');
  };

  const productosFiltrados = () => {
    if (tipoProducto) {
      const [tipo, subtipo] = tipoProducto.split(' - ');
      if (subtipo) {
        return productosOrdenados.filter((producto) => producto.tipo === tipo && producto.subtipo === subtipo);
      } else {
        return productosOrdenados.filter((producto) => producto.tipo === tipo);
      }
    } else {
      return productosOrdenados;
    }
  };

  const productosPaginados = () => {
    const inicio = (pagina - 1) * productosPorPagina;
    const fin = inicio + productosPorPagina;
    return productosFiltrados().slice(inicio, fin);
  };

  const cantidadTotalDePaginas = Math.ceil(productosFiltrados().length / productosPorPagina);

  const proveedoresValidos = ['SUEÑOLANDIA', 'FAMESA', 'MORADA', 'RENE DUBON'];

  const handleTipoChange = (e) => {
    setTipo(e.target.value);
    if (e.target.value.toLowerCase() === 'sala' || e.target.value.toLowerCase() === 'cama' || e.target.value.toLowerCase() === 'comedor') {
      setSubtipo('');
    } else {
      setSubtipo('');
    }
  };

  return (
    <div className="container-fluid mt-5 p-5">
      <ToastContainer />
      <h1 className="text-center mb-5">Administración de Productos</h1>
      <form className="mt-5">
        <div className="form-group">
          <div className="row">
            <div className="col-md-4">
              <label>Proveedor</label>
              <select className="form-control" value={proveedor} onChange={(e) => setProveedor(e.target.value)}>
                <option value="">Seleccione un proveedor</option>
                {proveedoresValidos.map((proveedor) => (
                  <option key={proveedor} value={proveedor}>
                    {proveedor}
                  </option>
                ))}
              </select>
            </div>
            <div className="col-md-4">
              <label>Tipo</label>
              <input type="text" className="form-control" value={tipo} onChange={handleTipoChange} />
            </div>
            <div className="col-md-4">
              {tipo.toLowerCase() === 'cama' && (
                <select className="form-control" value={subtipo} onChange={(e) => setSubtipo(e.target.value)}>
                  <option value="">Seleccione un subtipo</option>
                  <option value="IMPERIAL">IMPERIAL</option>
                  <option value="MATRIMONIAL">MATRIMONIAL</option>
                  <option value="QUEEN">QUEEN</option>
                  <option value="KING">KING</option>
                </select>
              )}
              {tipo.toLowerCase() === 'sala' && (
                <select className="form-control" value={subtipo} onChange={(e) => setSubtipo(e.target.value)}>
                  <option value="">Seleccione un subtipo</option>
                  <option value="LIGHT">LIGHT</option>
                  <option value="CLASICA">CLASICA</option>
                  <option value="PLUS">PLUS</option>
                  <option value="PREMIER">PREMIER</option>
                </select>
              )}
              {tipo.toLowerCase() === 'comedor' && (
                <select className="form-control" value={subtipo} onChange={(e) => setSubtipo(e.target.value)}>
                  <option value="">Seleccione un subtipo</option>
                  <option value="4 SILLAS">4 SILLAS</option>
                  <option value="6 SILLAS">6 SILLAS</option>
                  <option value="8 SILLAS">8 SILLAS</option>
                  <option value="10 SILLAS">10 SILLAS</option>
                  <option value="12 SILLAS">12 SILLAS</option>
                </select>
              )}
              {tipo.toLowerCase() !== 'cama' && tipo.toLowerCase() !== 'sala' && tipo.toLowerCase() !== 'comedor' && (
                <input type="text" className="form-control" value={subtipo} onChange={(e) => setSubtipo(e.target.value)} />
              )}
            </div>
          </div>
        </div>
        <div className="form-group">
          <div className="row">
            <div className="col-md-4">
              <label>SKU</label>
              <input type="text" className="form-control" value={sku} onChange={(e) => setSku(e.target.value)} />
            </div>
            <div className="col-md-8">
              <label>Descripción</label>
              <input type="text" className="form-control" value={descripcion} onChange={(e) => setDescripcion(e.target.value)} />
            </div>
          </div>
        </div>
        <div className="form-group">
          <div className="row">
            <div className="col-md-12">
              <label>Costo</label>
              <input type="number" className="form-control" value={costo} onChange={(e) => setCosto(Number(e.target.value))} />
            </div>
          </div>
        </div>
        {idEditar ? (
          <button type="button" className="btn btn-primary" onClick={() => editarProducto({ id: idEditar, proveedor, tipo, subtipo, sku, descripcion, costo })}>
            Guardar cambios
          </button>
        ) : (
          <button type="button" className="btn btn-success" onClick={agregarProducto}>
            Agregar
          </button>
        )}
      </form>
      <div className="row mt-5">
        <div className="col-md-2">
          <h5>Tipo de producto</h5>
          <ul>
            {Object.keys(tiposDeProducto()).map((tipo) => (
              <li key={tipo}>
                <a href="#" onClick={(e) => {
                  e.preventDefault();
                  setTipoProducto(tipo);
                }}>
                  {tipo} ({tiposDeProducto()[tipo]})
                </a>
                {productos.some((producto) => producto.tipo === tipo && producto.subtipo) && (
                  <ul>
                    {productos
                      .filter((producto) => producto.tipo === tipo)
                      .map((producto) => (
                        <li key={producto.subtipo}>
                          <a href="#" onClick={(e) => {
                            e.preventDefault();
                            setTipoProducto(`${tipo} - ${producto.subtipo}`);
                          }}>
                            {producto.subtipo} ({productos.filter((p) => p.tipo === tipo && p.subtipo === producto.subtipo).length})
                          </a>
                        </li>
                      ))
                      .filter((item, index, self) => self.findIndex((t) => t.key === item.key) === index)}
                  </ul>
                )}
              </li>
            ))}
            <li>
              <a href="#" onClick={(e) => {
                e.preventDefault();
                mostrarTodos();
              }}>
                Mostrar todos ({productos.length})
              </a>
            </li>
          </ul>
        </div>
        <div className="col-md-10">
          <table className="table table-striped table-hover">
            <thead className="thead-dark">
              <tr>
                <th>Proveedor</th>
                <th>Tipo</th>
                <th>Subtipo</th>
                <th>SKU</th>
                <th>Descripción</th>
                <th>Costo
                  <button type="button" className="btn btn-sm" onClick={ordenarProductos}>
                    {orden === 'asc' ? <i className="fas fa-sort-amount-down-alt"></i> : <i className="fas fa-sort-amount-up-alt"></i>} {orden === 'asc' ? 'D' : 'A'}
                  </button>
                </th>
                <th>Acciones</th>
              </tr>
            </thead>
            <tbody>
              {productosFiltrados().slice((pagina - 1) * productosPorPagina, pagina * productosPorPagina).map((producto) => (
                <tr key={producto.id}>
                  <td>{producto.proveedor}</td>
                  <td>{producto.tipo}</td>
                  <td>{producto.subtipo}</td>
                  <td>{producto.sku}</td>
                  <td>{producto.descripcion}</td>
                  <td>{producto.costo}</td>
                  <td>
                    <button type="button" className="btn btn-primary btn-sm mr-2" onClick={() => handleEditar(producto)}>
                      <i className="fas fa-edit"></i>
                    </button>
                    <button type="button" className="btn btn-danger btn-sm" onClick={() => handleEliminar(producto)}>
                      <i className="fas fa-trash-alt"></i>
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          <div className="row">
            <div className="col-md-6">
              {pagina > 1 && (
                <button type="button" className="btn btn-primary" onClick={() => setPagina(pagina - 1)}>
                  Anterior
                </button>
              )}
            </div>
            <div className="col-md-6 text-right">
              {pagina < cantidadTotalDePaginas && (
                <button type="button" className="btn btn-primary" onClick={() => setPagina(pagina + 1)}>
                  Siguiente
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdministracionProductos;