import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Navbar, Nav } from 'react-bootstrap';

const NavBar = ({ mostrarMenu, handleCerrarSesion }) => {
  const navigate = useNavigate();

  return (
    <Navbar bg="light" expand="lg">
      <Navbar.Brand href="#home">COTIZADOR DUPAZA</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link onClick={() => navigate('/AdministracionProductos')}>
            Administrar productos
          </Nav.Link>
          <Nav.Link onClick={() => navigate('/logs')}>
            Logs
          </Nav.Link>
          <Nav.Link onClick={() => navigate('/consulta-cotizaciones')}>
            Consulta cotizaciones
          </Nav.Link>
          <Nav.Link onClick={handleCerrarSesion}>
            Cerrar Sesión
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default NavBar;