import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { db } from '../firebase';
import { collection, onSnapshot } from 'firebase/firestore';
import { useNavigate } from 'react-router-dom';

const ProductRow = ({ product, updateProduct, removeProduct }) => {
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    updateProduct(product.id, { [name]: value });
  };

  return (
    <tr>
      <td>{product.sku}</td>
      <td>{product.descripcion}</td>
      <td>
        <input
          type="number"
          name="cantidad"
          value={product.cantidad || 1}
          min="1"
          onChange={handleInputChange}
        />
      </td>
      <td>Q{product.precioConMargen.toFixed(2)}</td>
      <td>
        <button className="btn btn-danger" onClick={() => removeProduct(product.id)}>Eliminar</button>
      </td>
    </tr>
  );
};

const ConsultaCotizaciones = () => {
  const [productos, setProductos] = useState([]);
  const [productosCotizar, setProductosCotizar] = useState([]);
  const [margenGanancia, setMargenGanancia] = useState(30);
  const [flete, setFlete] = useState('estandar');
  const [showSuggestedPrices, setShowSuggestedPrices] = useState(false);
  const [targetPrice, setTargetPrice] = useState();
  const [inputValue, setInputValue] = useState('');
  const [sugerencias, setSugerencias] = useState([]);
  const [sugerenciaSeleccionada, setSugerenciaSeleccionada] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const productosRef = collection(db, 'productos');
    onSnapshot(productosRef, (snapshot) => {
      const productos = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setProductos(productos);
    });
  }, [db]);

  const calcularPrecio = (costo) => {
    return costo / (1 - margenGanancia / 100);
  };

  const handleMargenChange = (nuevoMargen) => {
    if (nuevoMargen >= 30 && nuevoMargen <= 40) {
      setMargenGanancia(nuevoMargen);
    } else {
      alert('El margen debe estar entre 30% y 40%');
    }
  };

  const handleFleteChange = (e) => {
    setFlete(e.target.value);
  };

  const calculateSubtotal = () => {
    return productosCotizar.reduce((sum, product) => sum + calcularPrecio(product.costo) * product.cantidad, 0);
  };

  const calculateTotal = () => {
    const subtotal = calculateSubtotal();
    let fleteCost = 0;
    if (flete === 'estandar') {
      fleteCost = subtotal > 20000 ? 1000 : subtotal > 15000 ? 400 : 200;
    } else if (flete === 'largo') {
      fleteCost = subtotal > 20000 ? 1700 : subtotal > 15000 ? 730 : 365;
    }
    return subtotal + fleteCost;
  };

  const generateAdjustedPrices = (finalPrice) => {
    const subtotalWithoutFlete = calculateSubtotal();
    const fleteCost = calculateFleteCost(subtotalWithoutFlete);
    const totalWithoutFlete = finalPrice - fleteCost;

    const totalBasePrice = productosCotizar.reduce((sum, product) => sum + (product.costo * product.cantidad), 0);

    let totalCalculated = 0;
    const adjustedProducts = productosCotizar.map((product, index) => {
      const productShare = (product.costo * product.cantidad / totalBasePrice) * totalWithoutFlete;
      totalCalculated += productShare;

      return {
        ...product,
        precioSugerido: productShare,
      };
    });

    const difference = totalWithoutFlete - totalCalculated;

    const perProductAdjustment = Math.floor(difference / productosCotizar.length);

    adjustedProducts.forEach((product) => {
      product.precioSugerido = Math.round(product.precioSugerido + perProductAdjustment);
    });

    const finalTotal = adjustedProducts.reduce((sum, product) => sum + product.precioSugerido, 0);
    const finalDifference = totalWithoutFlete - finalTotal;

    if (finalDifference !== 0) {
      adjustedProducts[adjustedProducts.length - 1].precioSugerido += finalDifference;
    }

    adjustedProducts.forEach((product) => {
      product.precioSugeridoPorUnidad = product.precioSugerido / product.cantidad;
    });

    return adjustedProducts;
  };

  const calculateFleteCost = (subtotal) => {
    let fleteCost = 0;
    if (flete === 'estandar') {
      fleteCost = subtotal > 20000 ? 1000 : subtotal > 15000 ? 400 : 200;
    } else if (flete === 'largo') {
      fleteCost = subtotal > 20000 ? 1700 : subtotal > 15000 ? 730 : 365;
    }
    return fleteCost;
  };

  const handleShowSuggestedPrices = () => {
    if (targetPrice < calculateTotal() && targetPrice !== 0) {
      alert('El precio final deseado no puede ser menor al total');
    } else {
      setShowSuggestedPrices(!showSuggestedPrices);
    }
  };

  const updateProduct = (id, updatedFields) => {
    setProductosCotizar((prevProducts) =>
      prevProducts.map((product) =>
        product.id === id ? { ...product, ...updatedFields } : product
      )
    );
  };

  const removeProduct = (id) => {
    setProductosCotizar((prevProducts) =>
      prevProducts.filter((product) => product.id !== id)
    );
  };

  const handleNuevoProductoChange = (e) => {
    const valor = e.target.value;
    setInputValue(valor);
    buscarProductos(valor);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'ArrowDown') {
      if (sugerenciaSeleccionada === null) {
        setSugerenciaSeleccionada(0);
      } else if (sugerenciaSeleccionada < sugerencias.length - 1) {
        setSugerenciaSeleccionada(sugerenciaSeleccionada + 1);
      }
    } else if (e.key === 'ArrowUp') {
      if (sugerenciaSeleccionada > 0) {
        setSugerenciaSeleccionada(sugerenciaSeleccionada - 1);
      }
    } else if (e.key === 'Enter' || e.key === 'Tab') {
      if (sugerenciaSeleccionada !== null) {
        const sugerencia = sugerencias[sugerenciaSeleccionada];
        setProductosCotizar((prevProducts) => [...prevProducts, { ...sugerencia, cantidad: 1 }]);
        setInputValue('');
        setSugerencias([]);
        setSugerenciaSeleccionada(null);
      }
    }
  };

  const buscarProductos = (valor) => {
    const productosFiltrados = productos.filter((product) => {
      return product.sku.includes(valor) || product.descripcion.toLowerCase().includes(valor.toLowerCase());
    });
    setSugerencias(productosFiltrados);
  };

  return (
    <div className="container mt-5">
      <div className="text-center mb-5">
        <img src="/logo512.png" alt="Logo" width="200" height="200" />
      </div>
      {productosCotizar.length > 0 && (
        <table className="table table-striped table-hover">
          <thead className="thead-dark">
            <tr>
              <th>SKU</th>
              <th>Descripción</th>
              <th>Cantidad</th>
              <th>Precio</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            {productosCotizar.map((product, index) => (
              <ProductRow
                key={index}
                product={{
                  ...product,
                  precioConMargen: calcularPrecio(product.costo),
                }}
                updateProduct={(id, updatedFields) => {
                  updateProduct(product.id, updatedFields);
                }}
                removeProduct={(id) => {
                  removeProduct(product.id);
                }}
              />
            ))}
          </tbody>
        </table>
      )}
      <input
        type="text"
        value={inputValue}
        onChange={handleNuevoProductoChange}
        onKeyDown={handleKeyDown}
        placeholder="Escribe para buscar productos"
        className="form-control"
        autoFocus
      />
      {sugerencias.length > 0 && inputValue !== '' && (
        <ul className="list-group">
          {sugerencias.slice(0, 5).map((sugerencia, index) => (
            <li
              key={sugerencia.id}
              className={`list-group-item ${sugerenciaSeleccionada === index ? 'active' : ''}`}
              onClick={() => {
                setProductosCotizar((prevProducts) => [...prevProducts, { ...sugerencia, cantidad: 1 }]);
                setInputValue('');
                setSugerencias([]);
                setSugerenciaSeleccionada(null);
              }}
            >
              {sugerencia.descripcion} (SKU: {sugerencia.sku})
            </li>
          ))}
          {sugerencias.length > 5 && (
            <li className="list-group-item" onClick={() => {
              const mostrarMas = [...sugerencias.slice(0, 5), ...sugerencias.slice(5, 10)];
              setSugerencias(mostrarMas);
            }}>
             
            </li>
          )}
        </ul>
      )}
      <div className="row mt-5">
        <div className="col-md-6">
          <label>Margen de Ganancia: {margenGanancia}%</label>
          <button type="button" className="btn btn-primary mr-2" onClick={() => {
            if (margenGanancia < 40) {
              setMargenGanancia(margenGanancia + 1);
            }
          }}>
            +
          </button>
          <button type="button" className="btn btn-primary" onClick={() => {
            if (margenGanancia > 30) {
              setMargenGanancia(margenGanancia - 1);
            }
          }}>
            -
          </button>
        </div>
        <div className="col-md-6">
          <label>Tipo de Flete: </label>
          <select value={flete} onChange={handleFleteChange} className="form-control">
            <option value="estandar">Flete Estándar</option>
            <option value="largo">Flete Largo</option>
          </select>
        </div>
      </div>
      {productosCotizar.length > 0 && (
        <>
          <h4 className="mt-5 text-center">Subtotal: Q{calculateSubtotal().toFixed(2)}</h4>
          <h4 className="text-center">Flete: Q{calculateFleteCost(calculateSubtotal()).toFixed(2)}</h4>
          <h1 className="text-center">Total: Q{calculateTotal().toFixed(2)}</h1>
          <div className="row mt-5 justify-content-center">
            <div className="col-md-6 text-center">
              <label>Precio Final Deseado: </label>
              <input
                type="number"
                value={targetPrice}
                onChange={(e) => setTargetPrice(Number(e.target.value))}
                className="form-control"
              />
            </div>
            <div className="col-md-6">
              <button type="button" className="btn btn-primary" onClick={handleShowSuggestedPrices}>
                Mostrar Precios Sugeridos
              </button>
            </div>
          </div>
          {showSuggestedPrices && (
            <div className="mt-5">
              <h3>Sugerencia para lograr el total de Q{targetPrice}.00:</h3>
              <table className="table table-striped table-hover">
                <thead className="thead-dark">
                  <tr>
                    <th>SKU</th>
                    <th>Descripción</th>
                    <th>Cantidad</th>
                    <th>P/U</th>
                    <th>Margen</th>
                  </tr>
                </thead>
                <tbody>
                  {generateAdjustedPrices(targetPrice).map((product, index) => (
                    <tr key={index}>
                      <td>{product.sku}</td>
                      <td>{product.descripcion}</td>
                      <td>{product.cantidad}</td>
                      <td>Q{product.precioSugeridoPorUnidad.toFixed(2)}</td>
                      <td>{((1 - (product.costo / product.precioSugeridoPorUnidad)) * 100.).toFixed(2)}%</td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <button type="button" className="btn btn-primary" onClick={handleShowSuggestedPrices}>
                Cerrar
              </button>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default ConsultaCotizaciones;