import React, { useState, useCallback } from 'react';
import { getAuth, signInWithEmailAndPassword, sendPasswordResetEmail } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Login = ({ handleInicioSesion }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isResettingPassword, setIsResettingPassword] = useState(false);
  const [showResetPasswordModal, setShowResetPasswordModal] = useState(false);
  const [resetEmail, setResetEmail] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();

  const handleLoginSubmit = useCallback(async (e) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      const userCredential = await signInWithEmailAndPassword(getAuth(), email, password);
      localStorage.setItem('isLoggedIn', 'true');
      handleInicioSesion(true);
      navigate('/AdministracionProductos');
    } catch (error) {
      setError(error.message);
    } finally {
      setIsLoading(false);
    }
  }, [email, password, handleInicioSesion, navigate]);

  const handleResetPassword = useCallback(async (e) => {
    e.preventDefault();
    setIsResettingPassword(true);
    try {
      await sendPasswordResetEmail(getAuth(), resetEmail);
      toast.success('Correo de restablecimiento de contraseña enviado correctamente');
      setShowResetPasswordModal(false);
    } catch (error) {
      setError(error.message);
      toast.error('Error al enviar correo de restablecimiento de contraseña');
    } finally {
      setIsResettingPassword(false);
    }
  }, [resetEmail, setShowResetPasswordModal]);

  const handleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  return (
    <div className="container mt-5">
      <h2>Iniciar sesión</h2>
      <form onSubmit={handleLoginSubmit}>
        <div className="mb-3">
          <label>Correo electrónico:</label>
          <input type="email" className="form-control" value={email} onChange={(e) => setEmail(e.target.value)} />
        </div>
        <div className="mb-3">
          <label>Contraseña:</label>
          <div className="input-group">
            <input
              type={showPassword ? 'text' : 'password'}
              className="form-control"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <div className="input-group-append">
              <button type="button" className="btn btn-outline-secondary" onClick={handleShowPassword}>
                {showPassword ? <i className="fas fa-eye-slash" /> : <i className="fas fa-eye" />}
              </button>
            </div>
          </div>
        </div>
        <button type="submit" className="btn btn-primary" disabled={isLoading}>
          {isLoading ? 'Cargando...' : 'Iniciar sesión'}
        </button>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <p>
          {' '}
          <a href="#" onClick={(e) => {
            e.preventDefault();
            setShowResetPasswordModal(true);
          }}>
            ¿Olvidaste tu contraseña?{' '}
          </a>
        </p>
      </form>
      {showResetPasswordModal && (
        <div
          className="modal"
          style={{ display: 'block' }}
          tabIndex="-1"
          role="dialog"
          aria-modal="true"
          aria-hidden="false"
          aria-label="Modal de restablecimiento de contraseña"
        >
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Restablecer contraseña</h5>
                <button
                  type="button"
                  className="close"
                  onClick={(e) => {
                    e.preventDefault();
                    setShowResetPasswordModal(false);
                  }}
                  aria-label="Cerrar modal"
                >
                  &times;
                </button>
              </div>
              <div className="modal-body">
                <form onSubmit={handleResetPassword}>
                  <div className="mb-3">
                    <label>Correo electrónico:</label>
                    <input
                      type="email"
                      className="form-control"
                      value={resetEmail}
                      onChange={(e) => setResetEmail(e.target.value)}
                      aria-label="Correo electrónico"
                    />
                  </div>
                  <button
                    type="submit"
                    className="btn btn-secondary"
                    disabled={isResettingPassword}
                    aria-label="Restablecer contraseña"
                  >
                    {isResettingPassword ? 'Enviando...' : 'Restablecer contraseña'}
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      )}
      <ToastContainer />
    </div>
  );
};

export default Login;